.header {
  width: 93%;
  padding: 0.8rem 4%;
  background-color: white;
  border-bottom: 1px solid #e0e0e0;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__logo {
    display: flex;
    align-items: center;
  }

  &__icon {
    width: 22px;
    height: auto;
  }

  &__nav {
    display: flex;
    gap: 1.5rem;
  }

  &__link {
    text-decoration: none;
    color: #333;
    font-size: 1rem;
    font-weight: 500;
    transition: color 0.3s;

    &:hover {
      color: #007bff;
    }
  }
}
